.blur_effect {
  position: fixed;
  z-index: 3;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: rgba(0, 0, 0, 0.5);
  -webkit-transition: -webkit-filter .3s;
  transition: -webkit-filter .3s;
  -o-transition: filter .3s;
  transition: filter .3s;
  transition: filter .3s, -webkit-filter .3s;
  -webkit-filter: blur(2px);
  filter: blur(2px); }

.cancel_modal {
  position: fixed;
  top: 30vh; }

.cancel_modal div.card-content span.card-title.grey-text.text-darken-4 {
  margin-left: 0px; }



